import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { getDataSimple, addDataSimple } from '../redux/actions/table'
import { getWhere } from '../component/Where/index'
import * as tableActions from '../redux/actions/table'
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import Button from '@material-ui/core/Button';
import { Link } from 'react-router-dom'
import { mainWindow } from '../App'
const width = mainWindow.current.offsetWidth>800? 500: 240;

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textFieldSmall: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width
    },
    urls: {
        margin: theme.spacing.unit,
        width: width,
        maxHeight: 100,
        overflow: 'auto'
    },
    image: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        maxWidth: 500,
        maxHeight: 500
    },
    message: {
        width: width,
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: 'calc((100% - '+width+'px)/2)',
        marginRight: 'calc((100% - '+width+'px)/2)'
    },
});

const Cashbox = (
    (props) =>{
        const { classes } = props;
        useEffect(()=>{(async()=>{
            /*if (!(status.status==='active'&&['admin', 'manager', 'cashier', 'organizator'].includes(status.role))) {
                props.history.push('/')
            }*/
            await setCities(await getDataSimple({name: 'Города'}));
        })()},[])
        let [cities, setCities] = useState([]);
        let [city, setCity] = useState('');
        let [events, setEvents] = useState([]);
        let [_event, _setEvent] = useState({descriptionRu: '', where: {}, date: []});
        let handleCity = async(event) => {
            setCity(event.target.value)
            setEvents(await getDataSimple({name: 'СобытиеГородВсе', data: {city: event.target.value}}));
            handleEvent({target: {value: {descriptionRu: '', where: {}, date: []}}})
        };
        let handleEvent = async(event) => {
            let _event = event.target.value;
            if(!Array.isArray(_event.descriptionRu)&&_event.descriptionRu!==undefined)
                _event.descriptionRu = _event.descriptionRu.split('|');
            await _setEvent(_event)
        };
        let [breakTickets, setBreakTickets] = useState([]);
        let [type, setType] = useState('');
        let handleType = async(event) => {
            setType(event.target.value)
        };
        const types = ['повторяющиеся билеты',  'слетевшие билеты'];
        return (
            <div>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='город'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={city}
                    onChange={handleCity}
                >
                    {cities != undefined?
                        cities.map(option => (
                            <MenuItem key={option} value={option}>
                                {option}
                                </MenuItem>
                        ))
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='событие'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={_event}
                    onChange={handleEvent}
                >
                    {events != undefined?
                        events.map(option => {
                            return (<MenuItem key={option._id} value={option}>
                                {option.nameRu}
                            </MenuItem>)
                        })
                        :
                        null
                    }
                </TextField>
                <br/>
                <TextField
                    select
                    SelectProps={{
                        MenuProps: {
                            className: classes.menu,
                        },
                    }}
                    label='тип'
                    type='login'
                    className={classes.textField}
                    margin='normal'
                    value={type}
                    onChange={handleType}
                >
                    {types!= undefined?
                        types.map(option => {
                            return (<MenuItem key={option} value={option}>
                                {option}
                            </MenuItem>)
                        })
                        :
                        null
                    }
                </TextField>
                <br/>
                <Button variant='contained' color='secondary' className={classes.button} onClick={async()=>{
                    let data = await getDataSimple({name: 'Выявление', data: {type: type, event: _event.nameRu}})
                    setBreakTickets(data)
                    if(data.length===0)
                        alert('Проблем не выявлено!!!')
                }}>
                    Выявить
                </Button>
                <br/>
                <br/>
                <div className={classes.message}>
                    <b>количество:&nbsp;</b>{breakTickets.length}
                </div>
                <br/>
                {breakTickets != undefined?
                    breakTickets.map((option, idx) => {
                        return(
                            <>
                            <div className={classes.message} key={idx}>
                                <b>_id:&nbsp;</b>{option._id}<br/>
                                <b>время покупки:&nbsp;</b>{option.date_buy}<br/>
                                <b>время проведения:&nbsp;</b>{option.timeCarry}<br/>
                                <b>место:&nbsp;</b>{option.seat}&nbsp;&nbsp;
                                <b>ряд:&nbsp;</b>{option.row}&nbsp;&nbsp;
                                <b>сектор:&nbsp;</b>{option.sector}&nbsp;&nbsp;
                                <b>цена:&nbsp;</b>{option.price}<br/>
                                <b>статус:&nbsp;</b>{option.status}
                            </div>
                            {
                                type == 'слетевшие билеты'||breakTickets[idx+1]===undefined||(option.seat==breakTickets[idx+1].seat&&option.row==breakTickets[idx+1].row&&option.sector==breakTickets[idx+1].sector&&option.timeCarry==breakTickets[idx+1].timeCarry&&option.price==breakTickets[idx+1].price)?
                                    null
                                    :
                                    <>
                                    <hr />
                                    <br/>
                                    </>
                            }
                            <br/>
                            </>
                        )
                    })
                    :
                    null
                }
            </div>
        );
  })

function mapStateToProps (state) {
    return {
        table: state.table,
        user: state.user,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        tableActions: bindActionCreators(tableActions, dispatch),
    }
}

Cashbox.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(Cashbox));
